<template>
  <nav aria-label="Page navigation example">
    <ul class="pagination justify-content-center">
      <li class="page-item">
        <a
          class="page-link"
          @click.prevent="prePage(pages)"
          aria-label="Previous"
        >
          <span aria-hidden="true">&laquo;</span>
        </a>
      </li>
      <li
        class="page-item"
        :class="{ active: page === pages.current_page }"
        v-for="page in pages.total_pages"
        :key="page"
      >
        <a class="page-link" @click.prevent="updatePage(page)">{{ page }}</a>
      </li>
      <li class="page-item">
        <a class="page-link" @click.prevent="nextPage(pages)" aria-label="Next">
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>
    </ul>
  </nav>
</template>

<script>
// :pages="{ 頁碼資訊 }"
// @emitPages="更新頁面事件"
export default {
  props: ["pages"],
  methods: {
    updatePage(page) {
      this.$emit("emit-page", page);
    },
    prePage(pages) {
      const prePage = pages.current_page - 1;
      const hasPre = pages.has_pre;
      if (hasPre) {
        this.$emit("emit-page", prePage);
      }
    },
    nextPage(pages) {
      const nextPage = pages.current_page + 1;
      const hasNext = pages.has_next;
      if (hasNext) {
        this.$emit("emit-page", nextPage);
      }
    },
  },
};
</script>
